* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Google Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f8f9fa;
  min-height: 100vh;
  font-size: 14px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Professional CRM styling - Enhanced */
.MuiPaper-root {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  backdrop-filter: blur(10px);
}

.MuiPaper-root:hover {
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.12) !important;
  transform: translateY(-2px);
}

.MuiButton-root {
  text-transform: none !important;
  font-weight: 600 !important;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1) !important;
  letter-spacing: 0.01em !important;
}

.MuiButton-root:hover {
  transform: translateY(-2px);
}

.MuiButton-contained {
  box-shadow: 0 4px 12px rgba(25, 118, 210, 0.25) !important;
}

.MuiButton-contained:hover {
  box-shadow: 0 6px 16px rgba(25, 118, 210, 0.35) !important;
}

/* Smooth page transitions */
.fade-enter {
  opacity: 0;
}

.fade-enter-active {
  opacity: 1;
  transition: opacity 0.3s ease-in;
}

.fade-exit {
  opacity: 1;
}

.fade-exit-active {
  opacity: 0;
  transition: opacity 0.3s ease-out;
}

/* Status color indicators */
.status-new { color: #1976d2; }
.status-contacted { color: #0288d1; }
.status-qualified { color: #0288d1; }
.status-proposal_sent { color: #ed6c02; }
.status-negotiation { color: #ed6c02; }
.status-follow_up { color: #ed6c02; }
.status-won { color: #2e7d32; font-weight: 600; }
.status-lost { color: #d32f2f; }
.status-on_hold { color: #757575; }

.status-badge-new { background-color: #e3f2fd; color: #1976d2; }
.status-badge-contacted { background-color: #e1f5fe; color: #0288d1; }
.status-badge-qualified { background-color: #e1f5fe; color: #0288d1; }
.status-badge-proposal_sent { background-color: #fff3e0; color: #ed6c02; }
.status-badge-negotiation { background-color: #fff3e0; color: #ed6c02; }
.status-badge-follow_up { background-color: #fff3e0; color: #ed6c02; }
.status-badge-won { background-color: #e8f5e9; color: #2e7d32; font-weight: 600; }
.status-badge-lost { background-color: #ffebee; color: #d32f2f; }
.status-badge-on_hold { background-color: #f5f5f5; color: #757575; }

/* Enhanced Container and Layout */
.MuiContainer-root {
  position: relative;
}

/* Professional Scrollbar */
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #1976d2 0%, #1565c0 100%);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #1565c0 0%, #0d47a1 100%);
}

/* Enhanced Dialog Styling */
.MuiDialog-paper {
  border-radius: 16px !important;
  box-shadow: 0 8px 32px rgba(0,0,0,0.12) !important;
}

.MuiDialogTitle-root {
  background: linear-gradient(135deg, #f8fafc 0%, #ffffff 100%);
  border-bottom: 1px solid rgba(0,0,0,0.05);
  font-weight: 700 !important;
  letter-spacing: '-0.01em';
}

/* Enhanced TextField Styling */
.MuiOutlinedInput-root {
  transition: all 0.2s ease !important;
}

.MuiOutlinedInput-root.Mui-focused {
  box-shadow: 0 0 0 3px rgba(25, 118, 210, 0.1) !important;
}

/* Enhanced Chip Styling */
.MuiChip-root {
  transition: all 0.2s ease !important;
  font-weight: 500 !important;
}

/* Loading Spinner Enhancement */
.MuiCircularProgress-root {
  color: #1976d2 !important;
}

/* Enhanced Menu Styling */
.MuiMenu-paper {
  border-radius: 12px !important;
  box-shadow: 0 8px 24px rgba(0,0,0,0.12) !important;
  border: 1px solid rgba(0,0,0,0.05) !important;
  margin-top: 8px !important;
}

.MuiMenuItem-root {
  border-radius: 8px !important;
  margin: '4px 8px' !important;
  transition: all 0.2s ease !important;
}

.MuiMenuItem-root:hover {
  background: linear-gradient(90deg, rgba(25, 118, 210, 0.08) 0%, rgba(25, 118, 210, 0.04) 100%) !important;
}

/* Priority indicators */
.priority-low { color: #757575; }
.priority-medium { color: #ed6c02; }
.priority-high { color: #d32f2f; font-weight: 600; }

/* Professional table styling */
.MuiTableRow-root {
  transition: all 0.2s ease;
  border-left: 3px solid transparent;
}

.MuiTableRow-root:hover {
  background: linear-gradient(90deg, rgba(25, 118, 210, 0.05) 0%, rgba(25, 118, 210, 0.02) 100%);
  border-left-color: #1976d2;
  transform: translateX(2px);
}

.MuiTableHead .MuiTableRow-root {
  background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%);
  border-bottom: 2px solid #e2e8f0;
}

.MuiTableHead .MuiTableCell-root {
  font-weight: 700 !important;
  color: #1a202c !important;
  letter-spacing: 0.01em;
}

/* Card hover effects */
.card-hover {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}

.card-hover::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: left 0.5s;
}

.card-hover:hover::before {
  left: 100%;
}

.card-hover:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 28px rgba(0, 0, 0, 0.15);
  border-color: rgba(66, 133, 244, 0.2);
}

/* Smooth page transitions */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.page-enter {
  animation: fadeIn 0.3s ease-out;
}

/* Smooth scroll */
html {
  scroll-behavior: smooth;
}

/* Print styles for Invoice Preview - A4 Black & White */
@media print {
  @page {
    size: A4;
    margin: 0;
  }
  
  * {
    -webkit-print-color-adjust: exact !important;
    print-color-adjust: exact !important;
    color-adjust: exact !important;
  }
  
  body {
    background: white !important;
    color: black !important;
    margin: 0 !important;
    padding: 0 !important;
  }
  
  body * {
    visibility: hidden;
    color: #000000 !important;
  }
  
  .invoice-preview-container,
  .invoice-preview-container * {
    visibility: visible;
    color: #000000 !important;
    background: white !important;
  }
  
  .invoice-preview-container {
    position: relative !important;
    left: auto !important;
    top: auto !important;
    width: 210mm !important;
    max-width: 210mm !important;
    min-height: auto !important;
    max-height: none !important;
    height: auto !important;
    margin: 0 !important;
    padding: 5mm !important;
    box-shadow: none !important;
    page-break-after: auto !important;
    page-break-inside: avoid !important;
    overflow: visible !important;
    border: none !important;
    transform: none !important;
    scale: 1 !important;
    zoom: 1 !important;
    -webkit-transform: none !important;
    -moz-transform: none !important;
    -ms-transform: none !important;
    -o-transform: none !important;
  }
  
  /* Fixed layout for PDF export */
  .invoice-preview-container.pdf-export {
    transform: none !important;
    width: 210mm !important;
    max-width: 210mm !important;
    max-height: none !important;
    overflow: visible !important;
    padding: 5mm !important;
    margin: 0 auto !important;
    position: relative !important;
    left: auto !important;
    top: auto !important;
    -webkit-transform: none !important;
    -moz-transform: none !important;
    -ms-transform: none !important;
    -o-transform: none !important;
  }
  
  /* Ensure all child elements also don't have transforms */
  .invoice-preview-container * {
    transform: none !important;
    -webkit-transform: none !important;
    -moz-transform: none !important;
    -ms-transform: none !important;
    -o-transform: none !important;
  }
  
  /* Force black and white - remove all colors */
  .invoice-preview-container img {
    filter: grayscale(100%);
    -webkit-filter: grayscale(100%);
  }
  
  /* Hide everything except invoice */
  .no-print,
  .MuiAppBar-root,
  .MuiContainer-root > .MuiBox-root:first-child,
  button,
  .MuiTabs-root,
  .MuiPaper-root:not(.invoice-preview-container) {
    display: none !important;
    visibility: hidden !important;
  }
  
  /* Ensure all text is black */
  .invoice-preview-container p,
  .invoice-preview-container span,
  .invoice-preview-container div,
  .invoice-preview-container td,
  .invoice-preview-container th,
  .invoice-preview-container h1,
  .invoice-preview-container h2,
  .invoice-preview-container h3,
  .invoice-preview-container h4,
  .invoice-preview-container h5,
  .invoice-preview-container h6 {
    color: #000000 !important;
    background: transparent !important;
  }
  
  /* Ensure borders are black */
  .invoice-preview-container table,
  .invoice-preview-container td,
  .invoice-preview-container th,
  .invoice-preview-container .MuiBox-root {
    border-color: #000000 !important;
  }
  
  /* Remove all background colors except black title backgrounds */
  .invoice-preview-container .MuiBox-root,
  .invoice-preview-container .MuiTableCell-root,
  .invoice-preview-container .MuiTableRow-root {
    background: white !important;
    background-color: white !important;
  }
  
  /* Disable all hover transforms and scaling in invoice preview */
  .invoice-preview-container:hover,
  .invoice-preview-container .MuiTableRow-root:hover,
  .invoice-preview-container .MuiPaper-root:hover {
    transform: none !important;
    scale: none !important;
    translate: none !important;
  }
  
  /* Override global table row hover transform for invoice preview */
  .invoice-preview-container .MuiTableRow-root {
    transition: none !important;
    border-left: none !important;
  }
  
  .invoice-preview-container .MuiTableRow-root:hover {
    transform: none !important;
    translate: none !important;
    background: white !important;
    border-left-color: transparent !important;
  }
  
  /* Keep black backgrounds for section titles */
  .invoice-preview-container .MuiBox-root[style*="background-color: rgb(0, 0, 0)"],
  .invoice-preview-container .MuiBox-root[style*="background-color:#000000"],
  .invoice-preview-container .MuiBox-root[style*="background-color: #000000"] {
    background: #000000 !important;
    background-color: #000000 !important;
  }
  
  /* Table header with black background */
  .invoice-preview-container .MuiTableCell-root[style*="background-color: rgb(0, 0, 0)"],
  .invoice-preview-container .MuiTableCell-root[style*="background-color:#000000"],
  .invoice-preview-container .MuiTableCell-root[style*="background-color: #000000"] {
    background: #000000 !important;
    background-color: #000000 !important;
    color: #ffffff !important;
  }
  
  /* Grand total row - white background (black and white only) */
  .invoice-preview-container .MuiTableRow-root:last-child {
    background: white !important;
  }
  
  /* Prevent page breaks */
  .invoice-preview-container * {
    page-break-inside: avoid !important;
  }
}

